* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #1b1b1b;
}

h2 {
    color: white;
    text-align: center;
    margin: 10px;
}

.calculator {
    position: relative;
    display: grid;

}

.calculator .value {
    grid-column: span 4;
    height: 100px;
    text-align: right;
    border: none;
    outline: none;
    padding: 10px;
    font-size: 18px;
    margin: 3px auto;
    border-radius: 2px;
    background-color: rgba(234, 240, 213, 0.877);
}

.calculator span {
    display: grid;
    width: 60px;
    height: 60px;
    background: #292929;
    place-items: center;
    border: 1px solid rgba(0, 0, 0, 0.1);
    color: #fff;
    cursor: pointer;
}

.calculator span.clear {
    grid-column: span 2;
    width: 120px;
    background: rgb(187, 122, 25);
}

span:hover {
    color: aqua;
    background: rgb(92, 46, 46);
    transition: 0.2s ease-out;
    scale: 1.002;
}